<template>
  <div class="dataRow" :class="isSelected ? 'red' : ''">
    <span> {{ hour }} h => </span>
    <span> {{ value }} </span>
    <span> ‚Ç¨/kWh </span>
  </div>
</template>

<script>
import {ref} from "vue";

export default {
  name: 'DataRow',
  props: {
    data: {
      type: Object
    },
    isSelected: Boolean
  },
  setup(props) {
    const hour = ref(props.data.Hora.split('-')[0]);
    const value = ref(props.data.PCB);
    return {
      hour,
      value,
    }
  }
}
</script>
<style scoped>
.red {
  background-color: red !important;
}

.dataRow {
  min-width: 200px;
  min-height: 15px;
  border: solid black .1rem;
  background-color: aqua;
}
</style>
