<template>
  <div class="container">
    <custom-datepicker @update:modelValue="newValue => picked = newValue"/>
    <div v-if="!loading && !error && data && data.length">
      <VisualizeData :data="data" :startIndex=bestIndex :endIndex="Math.ceil(bestIndex + hours - 1)"/>
    </div>
    <div v-if="error">
      <span>Todavia no estan disponibles los precios de ma√±ana.</span>
    </div>
    <div class="hoursPicker">
      <article>Selecciona el numero de horas que estara en funcionamiento. Por ejemplo:<br/>
        Lavadoras 2.5 horas<br/>
        Lavavajillas 3.5 horas<br/></article>
      <input type="number" step="0.5" min="0" max="20" v-model="hours"/>
    </div>
  </div>
</template>

<script>

import VisualizeData from './components/VisualizeData.vue'
import {onMounted, ref, computed, watch} from "vue";
import CustomDatepicker from "@/components/CustomDatepicker";

export default {
  name: 'App',
  components: {
    CustomDatepicker,
    VisualizeData
  },
  setup() {
    const picked = ref(null);
    const data = ref(null);
    const loading = ref(true);
    const error = ref(false);
    const hours = ref(0);

    const bestIndex = computed(() => {
      if (!data.value || !hours.value) return -1;
      const result = [];
      let acum = 0;
      for (let i = 0; i < data.value.length - Math.ceil(hours.value); i++) {
        for (let j = i; j < i + Math.ceil(hours.value); j++) {
          acum = acum + Number(data.value[j].PCB);
        }
        result.push(acum.toFixed(5));
        acum = 0;
      }
      const min = Math.min(...result).toFixed(5)
      return result.findIndex((num) => num === min)
    });


    const fetchData = (pickedDate) => {
      loading.value = true;
      error.value = false;
      return fetch(`https://api.esios.ree.es/archives/70/download_json?locale=en&date=${pickedDate}`, {
        method: 'get',
        headers: {
          'content-type': 'application/json',
        },
      })
          .then(res => res.json())
          .then(json => json.PVPC)
          .then(json => {
            data.value = json.map(obj => {
              obj.PCB = (obj.PCB.replace(',', '.') / 1000).toFixed(5);
              return obj;
            });
            return json;
          }).catch(() => error.value = true)
          .finally(() => loading.value = false);
    }
    watch(() => picked.value, (first) => {
      fetchData(first.toLocaleDateString().split('T')[0]);
    })
    onMounted(() => fetchData(new Date().toLocaleDateString().split('T')[0]))
    return {
      data,
      loading,
      error,
      hours,
      bestIndex,
      picked,
    }
  }
}
</script>

<style>
body {
  font-size: 1.5rem;
}

.container {
  display: flex;
  gap: 5%;
}
.hoursPicker{
  max-width: 25%;
  display: flex;
  flex-direction: column;
}
</style>
